import request from '@/utils/request'
import qs from 'qs'

//名单分配查询列表
export function getListIssued(data) {
  return request({
    url: '/tpqydwt_app/dsqyh/listIssued/getListIssued',
    method: 'post',
    data:data
  })
}

//客户详情查询列表
export function getCustomerList(data) {
  return request({
    url: 'tpqydwt_app/dsqyh/listIssued/getCustomerListBybatchId',
    method: 'post',
    data:data
  })
}

